@media only screen and (max-width: 700px) {
  .u-form{
    margin: auto;
    height: 100vh;
    padding: 12px;
    color: aliceblue;
    width: 70%;
    background-color: #151718;
  }
  .update{
    height: 100%;
    width: 100%;
    padding-top: 10vh;
    padding-bottom: 12px;
    background-color: aqua;
  }
  .crud {
    /* background-color:red; */
    /* display: flex; */
    /* flex-wrap: wrap; */
    flex-direction: column;
    border: 2px solid red;
  }
  .left-t {
    width: 50%;
    margin: 12px auto;
    text-align: center;
  }
  .home {
    /* margin-top: 12vh; */
    height: 100%;
    background-color: rgb(108, 145, 150);
    width: 100%;
    display: flex;
    color: rgb(190, 192, 193);
    justify-content: center;
    align-items: center;
    flex-direction: column-reverse;
  }
  .image {
    color: aliceblue;
    margin-top: 10vh;
    margin-bottom: 6vh;
    background-color: rgb(253, 255, 255);
    height: 25vh;
    border: 2px solid rgb(251, 11, 11);
    border-radius: 30px;
    box-shadow: 2px 2px white;
  }
  .left {
    font-family: "sans-serif";
    width: 50%;
    
color: rgb(255, 255, 255);
    font-size: 30px;
